import React, { useRef, useState, useEffect } from "react"
import CryptoJS from "crypto-js";
//const CryptoJS = require("crypto-js")
import {  FormControl, Form, Container, Row, Col } from "react-bootstrap";

  

export default function DesktopNoStorage(){
    const [defaultPassLength, setDefaultPassLength] = useState(16);
    const [generatedPassword, setGeneratedPassword] = useState("");
    const form1 = useRef()
    const form2 = useRef()
    const passLengthSlider = useRef()
    const password = useRef()

    let letterDown = "abcdefghijklmnopqrstuvwxzy";
    let letterUp = "ABCDEFGHIJKLMNOPQRSTUVWXZY";
    let number = "0123456789";
    let special = "|/*-+=3-_()&^%$#@!~.?><,";
    
    function generatePassword(username, password, site, length){
        let characters = ""
        if(form2.current[0].checked) characters += letterDown;
        if(form2.current[1].checked) characters += letterUp;
        if(form2.current[2].checked) characters += number;
        if(form2.current[3].checked) characters += special;
        if(characters.length === 0) return;
        
        let sum = username+password+site
        let res = ""
        let prevCode = 1
    
        sum = CryptoJS.MD5(sum).toString()
                +   CryptoJS.MD5(sum).toString()
                +   CryptoJS.MD5(sum).toString()
                +   CryptoJS.MD5(sum).toString()
        
        for (let index = 0; index < length; index++) {
            res += characters[ ((sum.charCodeAt(index)*prevCode*(index+length))%characters.length) ]
            prevCode += sum.charCodeAt(index)
        }
        
        setGeneratedPassword(res)
    }

    async function genPassword(){
        await form1; 
        await form2;
        if(form1.current[0].value.length !== 0 && form1.current[1].value.length !== 0 && form1.current[2].value.length !== 0) 
            generatePassword(form1.current[0].value, form1.current[1].value, form1.current[2].value, defaultPassLength)
    }
    genPassword()
    return (
        <div className="h-100 d-flex flex-column justify-content-center align-items-center">
            <Form className="w-50" ref={form1}> 
                <FormControl placeholder="Username" aria-describedby="basic-addon1" className="mb-2" onChange={() => genPassword()}/>
                <FormControl type="password" placeholder="Password" aria-describedby="basic-addon1" className="mb-2" onChange={() => genPassword()}/>
                <FormControl placeholder="Url" aria-describedby="basic-addon1" className="mb-2" onChange={() => genPassword()}/>
            </Form>
            <Form.Range min={8} max={128} defaultValue={defaultPassLength} step={0} className="w-50" ref={passLengthSlider} onChange={ (e) => { setDefaultPassLength(e.target.value)} }/>
            <span>{defaultPassLength}</span>
            <Form ref={form2}>
                <Container className="w-75 text-center d-flex justify-items-center">
                        <Row>
                            <Col className="text-center">
                                <div key={1} className="mb-3 width-200">
                                    <Form.Check
                                        htmlFor="group1"
                                        inline
                                        label="Kis betuk"
                                        id="group1"
                                        type="checkbox"
                                        onChange={() => genPassword()}
                                    />
                                </div>
                            </Col>
                            <Col>
                                <div key={2} className="mb-3 width-200">
                                    <Form.Check
                                        htmlFor="group2"
                                        inline
                                        label="Nagy betuk"
                                        id="group2"
                                        type="checkbox"
                                        onChange={() => genPassword()}
                                    />
                                </div>
                            </Col>
                            <Col>
                                <div key={3} className="mb-3 width-200">
                                    <Form.Check
                                        htmlFor="group3"
                                        inline
                                        label="Szamok"
                                        id="group3"
                                        type="checkbox"
                                        onChange={() => genPassword()}
                                    />
                                </div>
                            </Col>
                            <Col>
                                <div key={4} className="mb-3 width-200">
                                    <Form.Check
                                        htmlFor="group4"
                                        inline
                                        label="Specialis karakterek"
                                        id="group4"
                                        type="checkbox"
                                        onChange={() => genPassword()}
                                    />
                                </div> 
                            </Col>
                        </Row>
                    </Container>
                </Form>
                <span className="w-50 border bg-primary rounded-3 text-light fs-5 p-1 text-break" ref={password}>
                    {generatedPassword}
                </span>
        </div>
    )
}