import React, { useState } from "react"
import { BrowserView, MobileView } from "react-device-detect"
import "bootstrap/dist/css/bootstrap.min.css"
import { Container, Row, Col } from "react-bootstrap"
import MobileStorageVersion from "./mobilStorageVersion"
import DesktopStorageVersion from "./desktopVersion"
import DesktopNoStorageVersion from "./desktopNoStorage"

import Panel from "./panel"
import "./style/grid.css"

export function TopMenu(props) {
	function changeMenu() {
		props.func(props.index)
	}
	return (
		<div className={props.value === props.index ? "border-bottom border-primary border-3" : ""} onClick={changeMenu}>
			{props.text}
		</div>
	)
}

export default function Navbar(props) {
	const [topmenu, setTopmenu] = useState(1)
	return (
		<>
		<div className="grid-main">
			<Row className="text-center">
				<Col className="ps-0 pe-0">
					<TopMenu text="Tarolás alapú" index={1} value={topmenu} func={setTopmenu}/>
				</Col>
				<Col className="ps-0 pe-0">
					<TopMenu text="Tudás alapú" index={2} value={topmenu} func={setTopmenu} />
				</Col>
			</Row>
			<div className="text-center h-test w-100">
				<BrowserView className="p-0 w-100 h-100">
					<Panel index={1} value={topmenu} className="w-100 h-100">
						<DesktopStorageVersion data={props.data} getData={props.getData} updateAddItem={props.updateAddItem}/>
					</Panel>
					<Panel index={2} value={topmenu} className="h-100">
						<DesktopNoStorageVersion />
					</Panel>
				</BrowserView>
				<MobileView>
					<Panel index={1} value={topmenu} className="m-1 mt-3 text-center">
						<MobileStorageVersion data={props.data} getData={props.getData} updateAddItem={props.updateAddItem}/>
					</Panel>
					<Panel index={2} value={topmenu} className="m-1 mt-3 text-center">
						<DesktopNoStorageVersion />
					</Panel>
				</MobileView>
			</div>
		</div>
		</>
		
	)
}
