function test(){
	console.log("main",data)
}
function updateAddItem(PMNav, type, value){
	console.log(PMNav, type, value);
	if(type){
		switch (PMNav.length) {
			case 0:
				data = [...data, value]
				console.log("root", data);
				break;
			case 1:
				data[PMNav[0]].list = [...data[PMNav[0]].list, value]
				break;
			case 2:
				data[PMNav[0]].list[PMNav[1]].list = [...data[PMNav[0]].list[PMNav[1]].list, value]
				break;
			default:
				break;
		}
	}else{
		switch (PMNav.length) {
			case 1:
				value.list = data[PMNav[0]].list
				data[PMNav[0]] = value
				break;
			case 2:
				value.list = data[PMNav[0]].list[PMNav[1]].list
				data[PMNav[0]].list[PMNav[1]] = value
				break;
			case 3:
				data[PMNav[0]].list[PMNav[1]].list[PMNav[2]] = value
				break;
			default:
				break;
		}
	}
}
const getData = () => data


function App() {
	return (
		<div className="full-size">
			<Navbar className="h-100" data={data} f={test} getData={getData} updateAddItem={updateAddItem}></Navbar>
			
		</div>
	)
}

export default App



