import React, { useState, useEffect, useRef} from "react";
import { ButtonGroup, Button, InputGroup, FormControl, Form } from "react-bootstrap";
import Panel from "./panel";
import DeleteIcon from "bootstrap-icons/icons/trash.svg";
import EditIcon from "bootstrap-icons/icons/gear.svg";
import AddIcon from "bootstrap-icons/icons/plus-lg.svg";
import LockIcon from "bootstrap-icons/icons/lock.svg";
import ProfileIcon from "bootstrap-icons/icons/person.svg";
import PasswordIcon from "bootstrap-icons/icons/key.svg";
import CheckIcon from "bootstrap-icons/icons/check-lg.svg";
import CrossIcon from "bootstrap-icons/icons/x-lg.svg";
import showPass from "bootstrap-icons/icons/eye.svg";
import nShowPass from "bootstrap-icons/icons/eye-slash.svg";
import ReloadIcon from "bootstrap-icons/icons/arrow-clockwise.svg"; 

export default function MobileVersion(props) {
    const [PMNav, setPMNav] = useState([]);
    const [StateData, setStateData] = useState(props.getData());
    const [panelState, setPanelState] = useState(1);
    const [selectedPass, setselectedPass] = useState(0);
    const [deleteButton, toggleDeleteButton] = useState(false);
    const [updateButton, toggleUpdateButton] = useState(false);
    const [isAddItem, setIsAddItem] = useState(true);
    const [showPassword, setShowPassword] = useState("password");

    const name = useRef("")
    const username = useRef("")
    const password = useRef("")
    const password2 = useRef("")
    const notes = useRef("")

    const passLetterDown = useRef("")
    const passLetterUp = useRef("")
    const passNumber = useRef("")
    const passSpecial = useRef("")

    const passLengthSlide = useRef()
    const passLengthOut = useRef()

    let elem;
    let listValue;
    let defaultPassLength = 20

    ///password gen vars
    let letterDown = "abcdefghijklmnopqrstuvwxzy";
    let letterUp = "ABCDEFGHIJKLMNOPQRSTUVWXZY";
    let number = "0123456789";
    let special = "|/*-+=3-_()&^%$#@!~.?><,";

    function nextMenu(index) {
        if (PMNav.length < 2) setPMNav([...PMNav, index]);
        else {
            setselectedPass(index);
            switchPanel(2);
        }
    }

    function backMenu() {
        if (PMNav.length === 1) setPMNav([]);
        else if (PMNav.length === 2) setPMNav([PMNav[0]]);
    }

    function prepareDeleteItem(index = selectedPass) {
        setselectedPass(index);
        switchPanel(3);
    }
    function prepareUpdateItem(index = selectedPass) {
        setselectedPass(index);
        name.current.value = StateData[index].name
        if(PMNav.length === 2){
            username.current.value = StateData[index].username
            password.current.value = StateData[index].password
            notes.current.value = StateData[index].notes
        }
        
        setIsAddItem(false);
        switchPanel(4);
    }

    function deleteItem() {
        StateData.splice(selectedPass, 1);
        setselectedPass(0);
        switchPanel(1);
    }

    function switchPanel(index) {
        setPanelState(index);
    }

    function addItem() {
        setIsAddItem(true);

        name.current.value = ""
        if(PMNav.length === 2){
            username.current.value = ""
            password.current.value = ""
            notes.current.value = ""
        }

        switchPanel(4);
    }

    function changePasswordVisibility() {
        setShowPassword(showPassword === "password" ? "text" : "password");
    }

    function addEditConfirm() {
        if (isAddItem) {
            if(name.current.value !== ""){
                if (PMNav.length === 2) {
                    props.updateAddItem(PMNav, isAddItem, {
                        name: name.current.value,
                        icon: "",
                        username: username.current.value,
                        password: password.current.value,
                        url: "",
                        modified: "",
                        notes: notes.current.value,
                    })
                    switchPanel(1)
                } else {
                    props.updateAddItem(PMNav, isAddItem, {name: name.current.value, icon: "", list: []})
                    switchPanel(1)
                }
            }
            updateStateDataFN() 
            console.log("hozzaad");
        }else{
            console.log("modosit");
            if(name.current.value !== ""){
                if (PMNav.length === 2) {
                    props.updateAddItem([...PMNav, selectedPass], isAddItem, {
                        name: name.current.value,
                        icon: "",
                        username: username.current.value,
                        password: password.current.value,
                        url: "",
                        modified: "",
                        notes: notes.current.value,
                    })
                    switchPanel(1)
                }else{
                    props.updateAddItem([...PMNav, selectedPass], isAddItem, {name: name.current.value, icon: "", list: []})
                    switchPanel(1)
                }
            }
            setselectedPass(0)
            updateStateDataFN()
        }
    }

    function updateStateDataFN(){
        if (PMNav.length === 0) setStateData(props.getData());
        else if (PMNav.length === 1) setStateData(props.getData()[PMNav[0]].list);
        else if (PMNav.length === 2) setStateData(props.getData()[PMNav[0]].list[PMNav[1]].list);
    }

    function generatePass(){ 
        let charList = ""; 
        if(passLetterDown.current.checked) charList += letterDown
        if(passLetterUp.current.checked) charList += letterUp
        if(passNumber.current.checked) charList += number
        if(passSpecial.current.checked) charList += special

        
        if(charList.length > 0){
            let pass = ""
            for(let i = 0; i < passLengthSlide.current.value; i++)
                pass += charList.charAt(Math.random()*charList.length)
            password2.current.value = pass
        }
    }

    function applyPass(){
        password.current.value = password2.current.value
        switchPanel(4)
    }


    useEffect(() => {
        updateStateDataFN()
    }, [PMNav, props.getData()]);

    if (panelState === 2) elem = StateData[selectedPass].name;

    //console.log(StateData)

    listValue = (
        <div>
            <Panel index={1} value={panelState}>
                <ButtonGroup className="d-flex mb-2">
                    <Button className="btn-success" onClick={() => addItem()}>
                        <img src={AddIcon} alt="add icon" />
                    </Button>
                    <Button className="btn-danger">
                        <img src={DeleteIcon} alt="delete icon" onClick={() => toggleDeleteButton(deleteButton ? false : true)} />
                    </Button>
                    <Button className="btn-warning">
                        <img src={EditIcon} alt="delete icon" onClick={() => toggleUpdateButton(updateButton ? false : true)} />
                    </Button>
                    <Button className="btn-primary">
                        <img src={LockIcon} alt="lock icon" />
                    </Button>
                </ButtonGroup>
                <div>
                    {PMNav.length !== 0 ? (
                        <Button key={-1} variant="primary" className="rounded-3 border-secondary mb-2 w-100" onClick={() => backMenu()}>
                            Vissza
                        </Button>
                    ) : (
                        ""
                    )}
                    {StateData.map((value, index) => (
                        <ButtonGroup className="d-flex rounded-3 border-secondary mb-2 " key={index + 1}>
                            <Button variant="secondary" className="w-100" onClick={() => nextMenu(index)}>
                                {value.name}
                            </Button>
                            {deleteButton ? (
                                <Button className="btn-danger" onClick={() => prepareDeleteItem(index)}>
                                    <img src={DeleteIcon} alt="delete icon" />
                                </Button>
                            ) : (
                                ""
                            )}
                            {updateButton ? (
                                <Button className="btn-warning" onClick={() => prepareUpdateItem(index)}>
                                    <img src={EditIcon} alt="update icon" />
                                </Button>
                            ) : (
                                ""
                            )}
                        </ButtonGroup>
                    ))}
                </div>
            </Panel>

            <Panel index={2} value={panelState}>
                <ButtonGroup className="d-flex mb-2">
                    <Button className="btn-danger" onClick={() => prepareDeleteItem()}>
                        <img src={DeleteIcon} alt="delete icon" />
                    </Button>
                    <Button className="btn-warning">
                        <img src={EditIcon} alt="edit icon" onClick={() => prepareUpdateItem()}/>
                    </Button>
                </ButtonGroup>
                <Button key={-1} variant="primary" className="rounded-3 border-secondary mb-2 w-100" onClick={() => switchPanel(1)}>
                    Vissza
                </Button>
                <Button
                    className="btn-primary mb-2 w-100 text-start"
                    onClick={() => {
                        navigator.clipboard.writeText(StateData[selectedPass].username);
                    }}
                >
                    <span className="d-flex flex-row justify-content-center">
                        <img src={ProfileIcon} alt="profile icon" />
                        <span className="m-auto">Copy username</span>
                    </span>
                </Button>
                <Button
                    className="btn-primary mb-2 w-100 text-start"
                    onClick={() => {
                        navigator.clipboard.writeText(StateData[selectedPass].password);
                    }}
                >
                    <span className="d-flex flex-row justify-content-center">
                        <img src={PasswordIcon} alt="password icon" />
                        <span className="m-auto">Copy password</span>
                    </span>
                </Button>
                <div className="border border-secondary bg-secondary rounded-3 text-white">
                    <div>Selected:</div>
                    {elem}
                </div>
            </Panel>

            <Panel index={3} value={panelState} className="d-flex flex-column justify-content-end">
                <div className="text-danger h4">Torles</div>
                <div>Amenyiben torli kesobb nem lehet visszaallitani a torol erteket</div>
                <div className="mt-3">Biztos torolni</div>
                <div className="text-danger">{StateData.length !== 0 ? StateData[selectedPass].name : ""}</div>
                <div>szeretne?</div>
                <ButtonGroup className="mt-5 d-flex">
                    <Button className="btn-success" onClick={() => deleteItem()}>
                        <img src={CheckIcon} alt="delete icon" />
                    </Button>
                    <Button className="btn-danger" onClick={() => switchPanel(1)}>
                        <img src={CrossIcon} alt="edit icon" />
                    </Button>
                </ButtonGroup>
            </Panel>

            <Panel index={4} value={panelState} className="d-flex flex-column justify-content-end">
                {PMNav.length === 2 ? (
                    <Form>
                        <Form.Label className="h4 text-success">{isAddItem ? "ADD" : "Edit"}</Form.Label>
                        <FormControl placeholder="Name" ref={name} aria-label="Username" aria-describedby="basic-addon1" className="mb-2" />
                        <FormControl placeholder="Username / Email" ref={username} aria-label="Username" aria-describedby="basic-addon1" className="mb-2" />
                        <InputGroup className="mb-3">
                            <FormControl type={showPassword} ref={password} placeholder="Jelszo" aria-label="Username" aria-describedby="basic-addon1" />
                            <InputGroup.Text onClick={() => switchPanel(5)}>
                                <img src={EditIcon} alt="" />
                            </InputGroup.Text>
                            <InputGroup.Text onClick={() => changePasswordVisibility()}>
                                <img src={showPassword === "password" ? showPass : nShowPass} alt="" />
                            </InputGroup.Text>
                        </InputGroup>
                        <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                            <Form.Label>Notes</Form.Label>
                            <Form.Control ref={notes} as="textarea" rows={3} />
                        </Form.Group>
                    </Form>
                ) : (
                    <Form>
                        <Form.Label className="h4 text-success">{isAddItem ? "Hozzaadas" : "Modositas"}</Form.Label>
                        <FormControl 
                            placeholder="Nev" 
                            aria-label="Username" 
                            aria-describedby="basic-addon1" 
                            className="mb-2" 
                            ref={name}
                        />
                    </Form>
                )}
                <ButtonGroup className="mt-5 d-flex">
                    <Button className="btn-success" onClick={() => addEditConfirm()}>
                        <img src={CheckIcon} alt="delete icon" />
                    </Button>
                    <Button className="btn-danger" onClick={() => switchPanel(1)}>
                        <img src={CrossIcon} alt="edit icon" />
                    </Button>
                </ButtonGroup>
            </Panel>
            <Panel index={5} value={panelState} className="d-flex flex-column justify-content-end align-items-start">
                <InputGroup className="mb-3">
                    <FormControl type={showPassword} ref={password2} placeholder="Jelszo" aria-label="Username" aria-describedby="basic-addon1" />
                    <InputGroup.Text onClick={() => generatePass()}>
                        <img src={ReloadIcon} alt=""/>
                    </InputGroup.Text>
                    <InputGroup.Text onClick={() => changePasswordVisibility()}>
                        <img src={showPassword === "password" ? showPass : nShowPass} alt="" />
                    </InputGroup.Text>
                </InputGroup>
                <div className="d-flex align-items-center mb-3 w-100">
                    <Form.Range min={1} max={128} defaultValue={defaultPassLength} ref={passLengthSlide} onChange={ (e) => { passLengthOut.current.value = e.target.value; } }/> 
                    <Form.Control
                        className="w-25 text-center ms-2"
                        type="text"
                        aria-describedby="passwordHelpBlock"
                        ref={passLengthOut}
                        defaultValue={defaultPassLength}
                        onChange={ (e) => { passLengthSlide.current.value = e.target.value; } }
                    />
                </div>
                <div key={1} className="mb-3">
                    <Form.Check
                        ref={passLetterDown}
                        htmlFor="group1"
                        inline
                        label="Kis betuk"
                        id="group1"
                        type="checkbox"
                    />
                </div>
                <div key={2} className="mb-3">
                    <Form.Check
                        ref={passLetterUp}
                        htmlFor="group2"
                        inline
                        label="Nagy betuk"
                        id="group2"
                        type="checkbox"
                    />
                </div>
                <div key={3} className="mb-3">
                    <Form.Check
                        ref={passNumber}
                        htmlFor="group3"
                        inline
                        label="Szamok"
                        id="group3"
                        type="checkbox"
                    />
                </div>
                <div key={4} className="mb-3">
                    <Form.Check
                        ref={passSpecial}
                        htmlFor="group4"
                        inline
                        label="Specialis karakterek"
                        id="group4"
                        type="checkbox"
                    />
                </div>  
                <ButtonGroup className="mt-5 d-flex w-100">
                    <Button className="btn-success" onClick={() => applyPass()}>
                        <img src={CheckIcon} alt="Generate icon" />
                    </Button>
                    <Button className="btn-danger" onClick={() => switchPanel(4)}>
                        <img src={CrossIcon} alt="edit icon" />
                    </Button>
                </ButtonGroup>
            </Panel>
        </div>
    );

    return <div>{listValue}</div>;
}
