import React, { useRef, useState, useEffect } from "react"
import { ButtonGroup, Button, InputGroup, FormControl, Form, Container, Row, Col } from "react-bootstrap";
import { useFilePicker } from 'use-file-picker';
import Panel from "./panel"
import "bootstrap/dist/css/bootstrap.min.css"
import "./style/desktop.css"
import ArrowLeft from "bootstrap-icons/icons/caret-left-fill.svg";
import ArrowDown from "bootstrap-icons/icons/caret-down-fill.svg";
import showPass from "bootstrap-icons/icons/eye.svg";
import nShowPass from "bootstrap-icons/icons/eye-slash.svg";
import EditIcon from "bootstrap-icons/icons/gear.svg";
import ReloadIcon from "bootstrap-icons/icons/arrow-clockwise.svg";
import GroupIcon from "bootstrap-icons/icons/archive.svg";
import CategoryIcon from "bootstrap-icons/icons/card-list.svg";
import PasswordIcon from "bootstrap-icons/icons/key.svg";
import LockIcon from "bootstrap-icons/icons/lock.svg";
import CheckIcon from "bootstrap-icons/icons/check-lg.svg";
import CrossIcon from "bootstrap-icons/icons/x-lg.svg";
import ProfileIcon from "bootstrap-icons/icons/person.svg";


export default function DesktopVersion(props) {
    const [StateData, setStateData] = useState(props.getData());
    const [selectedPass, setSelectedPass] = useState([]);
    const [panelState, setPanelState] = useState(0);
    const [showPassword, setShowPassword] = useState("password");
    const [selectType, setSelectType] = useState(-1)
    const [selectSubType, setSelectSubType] = useState(-1)
    const [startMenu, setStartMenu] = useState(0)

    const DEFAUL_PANEL = 0
    const ADD_UPDATE_PANEL = 1
    const PASSWORDGEN_PANEL = 2
    const DEL_PANEL = 3
    
    let init = true;
    //var selectType = 0
    
    const leftSide = useRef()
    const rightSide = useRef()
    const form = useRef()
    const password2 = useRef("")
    const enterPassword = useRef("")
    const enterName = useRef("")
    let widthValue = 250

    const passLetterDown = useRef("")
    const passLetterUp = useRef("")
    const passNumber = useRef("")
    const passSpecial = useRef("")

    const passLengthSlide = useRef()
    const passLengthOut = useRef()

    const [openFileSelector, { filesContent, loading }] = useFilePicker({
        //accept: ["."]
    });

    let defaultPassLength = 20

    ///password gen vars
    let letterDown = "abcdefghijklmnopqrstuvwxzy";
    let letterUp = "ABCDEFGHIJKLMNOPQRSTUVWXZY";
    let number = "0123456789";
    let special = "|/*-+=3-_()&^%$#@!~.?><,";

    function switchPanel(index) {
        setPanelState(index);
    }

    function deleteItem() {
        console.log(selectType, selectedPass, StateData);
        if(selectType === 0) StateData.splice(selectedPass[0], 1) 
        if(selectType === 1) StateData[selectedPass[0]].list.splice(selectedPass[1], 1)
        if(selectType === 2) StateData[selectedPass[0]].list[selectedPass[1]].list.splice(selectedPass[2], 1)
        setSelectedPass(selectedPass.slice(0, selectType))
        setStateData(props.getData())
        BackToDefault()
    }
    async function preparePanelChange(type, panelId, subType = 1) {
        if(selectedPass.length > type || (selectedPass.length > type-1 && !subType)){
            switchPanel(panelId)
            setSelectType(type)
            setSelectSubType(subType)
            if(panelId === ADD_UPDATE_PANEL) {
                let sd
                if(type === 0) sd = StateData;
                if(type === 1) sd = StateData[selectedPass[0]].list;
                if(type === 2) sd = StateData[selectedPass[0]].list[selectedPass[1]].list;
                if(subType){
                    await form
                    await (form.current[0].value = sd[selectedPass[type]].name)
                    if(type === 2){
                        await (form.current[1].value = sd[selectedPass[type]].username)
                        await (form.current[2].value = sd[selectedPass[type]].password)
                        await (form.current[3].value = sd[selectedPass[type]].url)
                        await (form.current[4].value = sd[selectedPass[type]].notes)
                    }
                }else{
                    await form
                    await (form.current[0].value = "")
                    await (form.current[1].value = "")
                    await (form.current[2].value = "")
                    await (form.current[3].value = "")
                    await (form.current[4].value = "")
                }
            }
        }
    }
    function BackToDefault(){
        setSelectType(-1)
        setSelectSubType(-1)
        switchPanel(DEFAUL_PANEL)
    }

    function changePasswordVisibility() {
        setShowPassword(showPassword === "password" ? "text" : "password");
    }
    document.body.onresize = () => calcSidePanel()

    function calcSidePanel(e = null){
        if(e != null && e.clientX-leftSide.current.getBoundingClientRect().left > 50 && leftSide.current.parentNode.offsetWidth-50 > e.clientX-leftSide.current.getBoundingClientRect().left)
            widthValue = e.clientX-leftSide.current.getBoundingClientRect().left
        
        leftSide.current.style = `width: ${(widthValue+10)}px`
        rightSide.current.style = `width: ${document.body.offsetWidth-(widthValue+10)}px`
    }

    async function addUpdateExecute(){
        if (!selectSubType) {
            if(await form.current[0].value !== ""){
                if(await form.current[0].value !== ""){
                    if (selectType === 2) {
                        console.log(selectedPass.slice(0, selectType+selectSubType), selectSubType);
                        props.updateAddItem(selectedPass.slice(0, selectType+selectSubType), !selectSubType, {
                            name: (await form.current[0].value),
                            icon: "",
                            username: (await form.current[1].value),
                            password: (await form.current[2].value),
                            url: (await form.current[3].value),
                            modified: "",
                            notes: (await form.current[4].value),
                        })
                    }else{
                        props.updateAddItem(selectedPass.slice(0, selectType+selectSubType), !selectSubType, {name: (await form.current[0].value), icon: "", list: []})
                    }
                }
                BackToDefault()
            }
        }else{
            if(await form.current[0].value !== ""){
                if (selectType === 2) {
                    console.log(selectedPass.slice(0, selectType+selectSubType), selectSubType);
                    props.updateAddItem(selectedPass.slice(0, selectType+selectSubType), !selectSubType, {
                        name: (await form.current[0].value),
                        icon: "",
                        username: (await form.current[1].value),
                        password: (await form.current[2].value),
                        url: (await form.current[3].value),
                        modified: "",
                        notes: (await form.current[4].value),
                    })
                }else{
                    props.updateAddItem(selectedPass.slice(0, selectType+selectSubType), !selectSubType, {name: (await form.current[0].value), icon: "", list: []})
                }
                BackToDefault()
            }
        }
        setStateData(props.getData())
    }

    function openOrClose(e){
        console.log(e.target.parentNode.parentNode.lastChild);
        if(e.target.src !== window.location.href.substring(0, window.location.href.length-1)+ArrowDown){
            e.target.src = ArrowDown
            e.target.parentNode.parentNode.lastChild.classList.add("d-none")
        }else{
            e.target.src = ArrowLeft
            e.target.parentNode.parentNode.lastChild.classList.remove("d-none")
        }
    }
    
    function selectPass(param){
        setSelectedPass([selectedPass[0], selectedPass[1], param])
    }
    
    function generatePass(){ 
        let charList = ""; 
        if(passLetterDown.current.checked) charList += letterDown
        if(passLetterUp.current.checked) charList += letterUp
        if(passNumber.current.checked) charList += number
        if(passSpecial.current.checked) charList += special

        
        if(charList.length > 0){
            let pass = ""
            for(let i = 0; i < passLengthSlide.current.value; i++)
                pass += charList.charAt(Math.random()*charList.length)
            password2.current.value = pass
        }
    }

    async function setPassword(){
        await form;
        form.current[2].value = (await password2.current.value)
        if(showPassword === "text") changePasswordVisibility()
        switchPanel(ADD_UPDATE_PANEL)
    }
    console.log(selectedPass);

    async function fileSelector(){
        openFileSelector()
    }

    async function createDatabase(){
        /*dialog.showSaveDialog({
            title: "Create Database",
            defaultPath: "",
            filters: [
                {name: "JSON", extensions: ["json"]}
            ],
        }, async (filePath) => {
            if(filePath !== undefined){
                //await props.createDatabase(filePath)
                //setStateData(props.getData())
                console.log(filePath);
                
            }
            return;
        })*/
        if(true){
            BackToDefault()
        }
    }   

    async function enterDatabase(){
        if(true){
            BackToDefault()
        }
    }
    function lockDatabase(){
        //setStartMenu(0)
        //setPanelState(-1)
    }
    function copyUsername(){
        if(selectedPass.length === 3)
            navigator.clipboard.writeText(StateData[selectedPass[0]].list[selectedPass[1]].list[selectedPass[2]].username)
    }
    function copyPassword(){
        if(selectedPass.length === 3)
            navigator.clipboard.writeText(StateData[selectedPass[0]].list[selectedPass[1]].list[selectedPass[2]].password)
    }
    
    useEffect(() => {
        if(loading) console.log("asd", filesContent[0]);
        if(panelState === -1 && startMenu === 0 && filesContent.length !== 0){
            setStartMenu(2)
        }
    }, [filesContent])
//<input type="file" ref={fileSelector} className="d-none"/>

    return (
        <div className="w-100 h-100">
            <Panel index={-1} value={panelState} className="border h-100">
                {startMenu === 0? 
                    <div className="border w-100 h-100 d-flex justify-content-center align-items-center flex-column">
                        <Button className="btn-secondary w-25 mb-3" onClick={() => fileSelector()} >Open database</Button>
                        <Button className="btn-secondary w-25" onClick={() => setStartMenu(1)}>Create Database</Button>
                    </div>:""
                }
                {startMenu === 1? 
                    <div className="border w-100 h-100 d-flex justify-content-center align-items-center flex-column">
                        Create Database 
                        <FormControl placeholder="Name" aria-label="Username" aria-describedby="basic-addon1" ref={enterName} className="mb-1 mt-3 w-50"/>
                        <FormControl placeholder="Password" aria-label="Username" aria-describedby="basic-addon1" ref={enterPassword} className="mb-3 mt-1 w-50"/>
                        <Button className="btn-success w-25 mb-3" onClick={() => enterDatabase()}>Create</Button>
                        <Button className="btn-danger w-25" onClick={() => setStartMenu(0)}>Cancel</Button>
                    </div>:""
                }
                {startMenu === 2? 
                    <div className="border w-100 h-100 d-flex justify-content-center align-items-center flex-column">
                        Open Database 
                        <FormControl placeholder="Password" aria-label="Username" aria-describedby="basic-addon1" ref={enterPassword} className="mb-3 mt-3 w-50"/>
                        <Button className="btn-success w-25 mb-3" onClick={() => enterDatabase()}>Enter</Button>
                        <Button className="btn-danger w-25" onClick={() => setStartMenu(0)}>Cancel</Button>
                    </div>:""
                }
            </Panel>
            <Panel index={0} value={panelState} className="grid-panel-0">
                <div className="top-panel d-flex container-fluid align-items-center">
                    <Row className="w-100">
                        <Col>
                            <fieldset>
                                <legend>Database</legend>
                                <img src={LockIcon} alt="" onClick={() => lockDatabase()}/>
                                <img src={ProfileIcon} alt="" onClick={() => copyUsername()}/>
                                <img src={PasswordIcon} alt="" onClick={() => copyPassword()}/>
                            </fieldset>
                        </Col>
                        <Col>
                            <fieldset>
                                <legend>Add</legend>
                                <img src={GroupIcon} alt="" onClick={() => preparePanelChange(   0, ADD_UPDATE_PANEL, 0)}/>
                                <img src={CategoryIcon} alt="" onClick={() => preparePanelChange(1, ADD_UPDATE_PANEL, 0)}/>
                                <img src={PasswordIcon} alt="" onClick={() => preparePanelChange(2, ADD_UPDATE_PANEL, 0)}/>
                            </fieldset>
                        </Col>
                        <Col>
                            <fieldset>
                                <legend>Edit</legend>
                                <img src={GroupIcon} alt="" onClick={() => preparePanelChange(   0, ADD_UPDATE_PANEL, 1)}/>
                                <img src={CategoryIcon} alt="" onClick={() => preparePanelChange(1, ADD_UPDATE_PANEL, 1)}/>
                                <img src={PasswordIcon} alt="" onClick={() => preparePanelChange(2, ADD_UPDATE_PANEL, 1)}/>
                            </fieldset>
                        </Col>
                        <Col>
                            <fieldset>
                                <legend>Remove</legend>
                                <img src={GroupIcon} alt="" onClick={() => preparePanelChange(   0, DEL_PANEL)}/>
                                <img src={CategoryIcon} alt="" onClick={() => preparePanelChange(1, DEL_PANEL)}/>
                                <img src={PasswordIcon} alt="" onClick={() => preparePanelChange(2, DEL_PANEL)}/>
                            </fieldset>
                        </Col>
                    </Row>
                </div>
                <div className="d-flex bottom-panel">
                    <div className="border d-flex flex-row" style={{width: widthValue+"px"}} ref={leftSide}>
                        <div className="w-100 d-flex flex-column overflow-scroll-y">
                            {StateData.map((value, mIndex) => (
                                <div className={(selectedPass.length > 0 && selectedPass[0] === mIndex ? "selected ":"") + "w-100 border d-flex flex-column"} key={mIndex}>
                                    <h5 className="ps-2 d-flex flex-row justify-content-between align-items-center pointer"  onClick={() => setSelectedPass([mIndex])}>{value.name}<img src={ArrowLeft} onClick={(e) => openOrClose(e)}/></h5>
                                    <div className="w-100 d-flex flex-column">
                                    {value.list.map((value, sIndex) =>(
                                        <div className={((selectedPass.length > 1 && selectedPass[1] === sIndex && selectedPass[0] === mIndex) ? "selected ":"notSelected ") + "text-start ps-3 pointer"} key={mIndex+"-"+sIndex} onClick={() => setSelectedPass([mIndex, sIndex])}>{value.name}</div>
                                    ))
                                    }
                                    </div>
                                </div>
                            ))}
                        </div>
                        <div className="resizer" draggable={true} onDrag={(e) => calcSidePanel(e)}></div>
                    </div>
                    <div className="border" style={{width: "100%"}} ref={rightSide} onLoad={() => calcSidePanel()}>
                        <div className="d-flex flex-column pointer  overflow-scroll-y">
                            {(selectedPass.length > 1)?
                                StateData[selectedPass[0]].list[selectedPass[1]].list.map((value, index) => <div key={"pass-"+index} className={selectedPass.length === 3 && selectedPass[2] === index? "w-100 border selected" : "w-100 border"} onClick={() => selectPass(index)}>{value.name}</div>):""
                            }   
                        </div>
                    </div>
                </div>
            </Panel>




            <Panel index={ADD_UPDATE_PANEL} value={panelState} className="h-100">
                {selectType === 2?
                    <div className="d-flex h-100 flex-column">
                        <Form ref={form}>
                        <span>{selectSubType?"Edit":"Add"}</span>
                        <div className="d-flex flex-column h-100 me-2">
                            <div className="d-flex flex-row align-items-center mb-2">
                                <div className="sidePanel">Name</div>
                                <FormControl placeholder="Name" aria-label="Username" aria-describedby="basic-addon1" className="" />
                            </div>
                            <div className="d-flex flex-row align-items-center mb-2">
                                <div className="sidePanel">Username</div>
                                <FormControl placeholder="Username / Email" aria-label="Username" aria-describedby="basic-addon1" className="" />
                            </div>
                            <div className="d-flex flex-row align-items-center mb-2">
                                <div className="sidePanel">Password</div>
                                <InputGroup className="">
                                    <FormControl type={showPassword} placeholder="Password" aria-label="Username" aria-describedby="basic-addon1" />
                                    <InputGroup.Text onClick={() => switchPanel(PASSWORDGEN_PANEL)}>
                                        <img src={EditIcon} alt="" />
                                    </InputGroup.Text>
                                    <InputGroup.Text onClick={() => changePasswordVisibility()}>
                                        <img src={showPassword === "password" ? showPass : nShowPass} alt="" />
                                    </InputGroup.Text>
                                </InputGroup>
                            </div>
                            <div className="d-flex flex-row align-items-center mt-2">
                                <div className="sidePanel">Url</div>
                                <FormControl placeholder="Url" aria-label="Username" aria-describedby="basic-addon1" className="mb-2" />
                            </div>
                            <div className="d-flex flex-row align-items-center mb-2">
                                <div className="sidePanel">Notes</div>
                                <Form.Group className="w-100">
                                    <Form.Control as="textarea" rows={3} />
                                </Form.Group>
                            </div>
                            <ButtonGroup className="mb-2 w-50 mt-auto align-self-end">
                                <Button className="btn-danger" onClick={() => BackToDefault()} >Cancel</Button>
                                <Button className="btn-success" onClick={() => addUpdateExecute()}>Save</Button>
                            </ButtonGroup>
                        </div>
                        </Form>
                    </div>
                    :
                    <div className="d-flex h-100 flex-column">
                        <span>{selectSubType?"Edit":"Add"}{selectSubType ? "":""}</span>
                        <div className="d-flex flex-column h-100 me-2">
                            <Form ref={form}>
                                <div className="d-flex flex-row align-items-center">
                                    <div className="sidePanel">Name</div>
                                    <FormControl placeholder="Name" aria-label="Username" aria-describedby="basic-addon1" className="" />
                                </div>
                            </Form>
                            <ButtonGroup className="mb-2 w-50 mt-auto align-self-end">
                                <Button className="btn-danger" onClick={() => BackToDefault()} >Cancel</Button>
                                <Button className="btn-success" onClick={() => addUpdateExecute()}>Save</Button>
                            </ButtonGroup>
                        </div>
                    </div>
                }
            </Panel>





            <Panel index={2} value={panelState} className="h-100">
                <div className="d-flex flex-column">
                <InputGroup className="mb-3">
                    <FormControl type={showPassword} ref={password2} placeholder="Jelszo" aria-label="Username" aria-describedby="basic-addon1" />
                    <InputGroup.Text onClick={() => generatePass()}>
                        <img src={ReloadIcon} alt=""/>
                    </InputGroup.Text>
                    <InputGroup.Text onClick={() => changePasswordVisibility()}>
                        <img src={showPassword === "password" ? showPass : nShowPass} alt="" />
                    </InputGroup.Text>
                </InputGroup>
                <div className="d-flex align-items-center mb-3 w-100">
                    <Form.Range min={1} max={128} defaultValue={defaultPassLength} ref={passLengthSlide} onChange={ (e) => { passLengthOut.current.value = e.target.value; } }/> 
                    <Form.Control
                        className="w-25 text-center ms-2"
                        type="text"
                        aria-describedby="passwordHelpBlock"
                        ref={passLengthOut}
                        defaultValue={defaultPassLength}
                        onChange={ (e) => { passLengthSlide.current.value = e.target.value; } }
                    />
                </div>
                <Container>
                    <Row>
                        <Col>
                            <div key={1} className="mb-3 width-200">
                                <Form.Check
                                    ref={passLetterDown}
                                    htmlFor="group1"
                                    inline
                                    label="Kis betuk"
                                    id="group1"
                                    type="checkbox"
                                />
                            </div>
                        </Col>
                        <Col>
                            <div key={2} className="mb-3 width-200">
                                <Form.Check
                                    ref={passLetterUp}
                                    htmlFor="group2"
                                    inline
                                    label="Nagy betuk"
                                    id="group2"
                                    type="checkbox"
                                />
                            </div>
                        </Col>
                        <Col>
                            <div key={3} className="mb-3 width-200">
                                <Form.Check
                                    ref={passNumber}
                                    htmlFor="group3"
                                    inline
                                    label="Szamok"
                                    id="group3"
                                    type="checkbox"
                                />
                            </div>
                        </Col>
                        <Col>
                            <div key={4} className="mb-3 width-200">
                                <Form.Check
                                    ref={passSpecial}
                                    htmlFor="group4"
                                    inline
                                    label="Specialis karakterek"
                                    id="group4"
                                    type="checkbox"
                                />
                            </div> 
                        </Col>
                    </Row>
                </Container>
                <ButtonGroup className="mb-2 w-50 mt-auto align-self-end">
                    <Button className="btn-danger" onClick={() => switchPanel(1)} >Cancel</Button>
                    <Button className="btn-success" onClick={() => setPassword()}>OK</Button>
                </ButtonGroup>
                </div>
            </Panel>





            <Panel index={3} value={panelState} className="h-100 grid-panel-3">
                <div className="d-flex flex-column">
                    <div className="text-danger h4">Torles</div>
                    <div>Amenyiben torli, kesobb nem lehet visszaallitani</div>
                    <div className="text-danger">
                        {selectType === 0 && selectSubType ? StateData[selectedPass[0]].name:""}
                        {selectType === 1 && selectSubType ? StateData[selectedPass[0]].list[selectedPass[1]].name:""}
                        {selectType === 2 && selectSubType ? StateData[selectedPass[0]].list[selectedPass[1]].list[selectedPass[2]].name:""}
                    </div>
                    <div className="mt-3">Biztos torolni</div>
                    <div className="text-danger"></div>
                    <div>szeretne?</div>
                </div>
                <ButtonGroup className="w-25">
                    <Button className="btn-success" onClick={() => deleteItem()}>
                        <img src={CheckIcon} alt="delete icon" />
                    </Button>
                    <Button className="btn-danger" onClick={() => BackToDefault()}>
                        <img src={CrossIcon} alt="edit icon" />
                    </Button>
                </ButtonGroup>
            </Panel>
        </div>
    )
}